@import url(https://fonts.googleapis.com/css2?family=Allerta+Stencil&display=swap);
.main-page__content {
    padding: 42px calc(50% - 500px);
    display: flex;
    justify-content: space-between;
}

.main-page__main-section {
    padding-left: 40px;
    box-sizing: border-box;
    width: 60%;
}

.main-page__search-box {
    margin-bottom: 35px;
}

.main-page__favorites {
    width: 35%;   
}
.header {
  background: #496ddb;
  padding: 20px calc(50% - 500px) 10px;
}

.header__title {
  /* font-family: 'Luckiest Guy', cursive; */

  font-family: "Allerta Stencil", sans-serif;

  color: #ffeedb;
  font-size: 45px;
  margin-left: 40px;
}

.search-box__form {
    display: flex;
}

.search-box__form-label {
    display: inline-block;
    font-size: 12px;
    margin-right: 10px;
}

.search-box__form-input {
    box-sizing: border-box;
    display: block;
    margin-top: 5px;
    width: 300px;
    font-size: 14px;
    padding: 5px 10px;
    border: 1px solid rgba(0, 8, 7, .5);
}

.search-box__form-submit {
    margin-top: 17px;
    padding: 5px 10px;
    border: none;
    transition: all 0.2s linear;
    color: #FFEEDB;
    background-color: rgba(0, 8, 7);
}

.search-box__form-submit:hover:not(:disabled) {
    opacity: 0.8;
    cursor: pointer;
    transition: all 0.2s linear;
    background-color: #FFEEDB;
    color: rgba(0, 8, 7);
}

.search-box__form-submit:disabled {
    opacity: 0.5;
    cursor: not-allowed;
}
.movie-item {
  padding: 20px;
  background-color: #ffeedb;
  display: flex;
}

.movie-item__poster {
  height: 150px;
  margin-right: 21px;
}

.movie-item__title {
  font-weight: 500;
  font-size: 21px;
  margin-bottom: 14px;
}

.movie-item__add-button {
  border: none;
  background-color: #496ddb;
  padding: 10px 15px;
  color: #ffeedb;
  font-size: 14px;
  transition: all 0.2s linear;
}

.movie-item__add-button:hover {
  /* cursor: pointer; */
  opacity: 0.9;
  transition: all 0.2s linear;
}

.disabled-button {
  background-color: #7696f5;

  cursor: not-allowed	;
}

.movies__item {
    margin-bottom: 21px;
}
.favorites {
  box-sizing: border-box;
  border: 1px solid rgba(0, 8, 7);
  padding: 30px 20px;
  position: fixed;

  width: 386px;
}

.favorites__name {
  box-sizing: border-box;
  width: 100%;
  font-size: 16px;
  padding: 5px 10px;
  border: 1px solid rgba(0, 8, 7, 0.5);
  margin-bottom: 24px;
}

.favorites__list {
  margin-bottom: 24px;
}

.favorites__save {
  border: none;
  background-color: #496ddb;
  padding: 10px 15px;
  color: #ffeedb;
  font-size: 14px;
  transition: all 0.2s linear;
}

.favorites__save:hover {
  cursor: pointer;
  opacity: 0.9;
  transition: all 0.2s linear;
}

/* Элемент списка  - название фильма и год */
.list-item {
  position: relative;
  display: flex;
  margin-top: 5px;
  margin-bottom: 5px;
}

/* Спан , который является кнопкой УДАЛИТЬ  */
.list-item__delete-button {
  width: 17px;
  height: 17px;
  color: white;

  background-color: tomato;

  display: flex;
  justify-content: center;
  align-items: center;

  border-radius: 50%;

  cursor: pointer;

  margin-right: 10px;
}

.link-to-another-page {
  color: #6aa9e0;
}

.list-page {
    padding: 42px calc(50% - 300px);
}

.list-page__title {
    font-size: 34px;
    margin-bottom: 26px;
}

.list-page ul li {
    font-size: 18px;
}


/* Элемент списка  - фильм */
.list-item-film {
  /* display: flex; */

  width: 300px;

  border:  1px solid rgb(145, 136, 136);

  border-radius: 10px;
}

.list-item-film:not(:last-of-type) {
  margin-bottom: 20px;
}


.list-item-poster {
  display: block;
  border-radius: 10px;

  margin-bottom: 4px;

  width:300px;

  height: 400px;
}


.link-to-imdb {
  text-decoration: none;
  position: relative;
  left: 2px;
  bottom: 2px;
}

/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
