.favorites {
  box-sizing: border-box;
  border: 1px solid rgba(0, 8, 7);
  padding: 30px 20px;
  position: fixed;

  width: 386px;
}

.favorites__name {
  box-sizing: border-box;
  width: 100%;
  font-size: 16px;
  padding: 5px 10px;
  border: 1px solid rgba(0, 8, 7, 0.5);
  margin-bottom: 24px;
}

.favorites__list {
  margin-bottom: 24px;
}

.favorites__save {
  border: none;
  background-color: #496ddb;
  padding: 10px 15px;
  color: #ffeedb;
  font-size: 14px;
  transition: all 0.2s linear;
}

.favorites__save:hover {
  cursor: pointer;
  opacity: 0.9;
  transition: all 0.2s linear;
}

/* Элемент списка  - название фильма и год */
.list-item {
  position: relative;
  display: flex;
  margin-top: 5px;
  margin-bottom: 5px;
}

/* Спан , который является кнопкой УДАЛИТЬ  */
.list-item__delete-button {
  width: 17px;
  height: 17px;
  color: white;

  background-color: tomato;

  display: flex;
  justify-content: center;
  align-items: center;

  border-radius: 50%;

  cursor: pointer;

  margin-right: 10px;
}

.link-to-another-page {
  color: #6aa9e0;
}
