.movie-item {
  padding: 20px;
  background-color: #ffeedb;
  display: flex;
}

.movie-item__poster {
  height: 150px;
  margin-right: 21px;
}

.movie-item__title {
  font-weight: 500;
  font-size: 21px;
  margin-bottom: 14px;
}

.movie-item__add-button {
  border: none;
  background-color: #496ddb;
  padding: 10px 15px;
  color: #ffeedb;
  font-size: 14px;
  transition: all 0.2s linear;
}

.movie-item__add-button:hover {
  /* cursor: pointer; */
  opacity: 0.9;
  transition: all 0.2s linear;
}

.disabled-button {
  background-color: #7696f5;

  cursor: not-allowed	;
}
