.list-page {
    padding: 42px calc(50% - 300px);
}

.list-page__title {
    font-size: 34px;
    margin-bottom: 26px;
}

.list-page ul li {
    font-size: 18px;
}


/* Элемент списка  - фильм */
.list-item-film {
  /* display: flex; */

  width: 300px;

  border:  1px solid rgb(145, 136, 136);

  border-radius: 10px;
}

.list-item-film:not(:last-of-type) {
  margin-bottom: 20px;
}


.list-item-poster {
  display: block;
  border-radius: 10px;

  margin-bottom: 4px;

  width:300px;

  height: 400px;
}


.link-to-imdb {
  text-decoration: none;
  position: relative;
  left: 2px;
  bottom: 2px;
}
